html { scroll-behavior: smooth; }

.header a {
    color: black;
}

.header>a {
    font-family: 'Courier New', Courier, monospace;
    font-size: xx-large;
    font-weight: bold;
    color: grey
}

.profile {
    display: -webkit-flex;
    display: flex
}

.profile-text {
    -webkit-flex: 1 1;
            flex: 1 1
}
.profile-img {
    width: 252px;
    height: 252px;
    border-radius: 50%;
    margin: 20px
}
.profile-image{
    z-index: 10;
    width: 152px;
    height: 152px;
    border-radius: 50%;
}

.parallax-container {
  }
.skill{
    width: 150px;
    height: 150px;
    border-radius: 100%;
    -webkit-transition: 1000ms;
    transition: 1000ms
}

.skill:hover {
    -webkit-transform: scale(1.3);
            transform: scale(1.3); /* (150% zoom - Note: if the zoom is too large, it will go outside of the viewport) */
  }
.footer-text{
    text-align: center;
    bottom: 0px;
    background-color: white;
    padding: 200px
}

.contact {
    width: 100px;
    height: 100px;
}

.social-media {
    width: 80px;
    height: 80px;
    float: right;
    margin-left: 20px
}
.card-image{
    width: 200px;
    height: 200px;
}


.blank-page{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.my-image{
    border-radius: 100%;
    width: 200px;
    height: 200px;
}
